import React, { Component } from "react";
import ConditionBase from "./conditionBase";
import configurations from "../data/configurations";

class Condition extends Component {
  constructor(props) {
    super(props);
    this.updateState = this.updateState.bind(this);
    this.state = {
      condition: {
        Diagnoses: null,
        Problems: null
      }
    };
    this.updateData = this.updateData.bind(this);
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.patientId !== this.props.patientId) this.updateData();
  }

  render() {
    console.log("condition render");
    return (
      <React.Fragment>
        <div className="col-lg-12 p-2 bg-dark text-light d-flex justify-content-between shadow">
          <span>Condition</span>
        </div>
        <div className="condition d-flex flex-column">
          <ConditionBase
            model="Diagnosis"
            data={this.state.condition.Diagnoses}
          ></ConditionBase>
          <ConditionBase
            model="Problems"
            data={this.state.condition.Problems}
          ></ConditionBase>
        </div>
      </React.Fragment>
    );
  }

  updateData = () => {
    if (this.props.patientId == 0) return;

    console.log("Loading condition data!");
    fetch(
      configurations.baseURL + "/Condition/Id/" + this.props.patientId
    ).then(
      res => {
        if (res.status == 200) res.json().then(this.udpateState);
      },
      error => {
        console.log("fetch condition failed", error);
        this.updateState({
          Diagnoses: null,
          Problems: null
        });
      }
    );
  };

  updateState(s) {
    console.log("update state condition");
    this.setState({
      condition: s
    });
  }
}

export default Condition;
